.footer {
    padding: 25px;
}

.footer p {
    text-align: center;
    line-height: 27px;
}

.footer span {
    background: -webkit-linear-gradient(-70deg, #c12525 0%, #01b517 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.footer-img img {
    border-width: 8px;
    border-style: solid;
    border-color: #B667D0;
}

.social-links {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
}

.social-links > p {
    margin: 5px 10px;
}

.credits p {
    font-size: 20px;
    font-style: italic;
    color: #555555;
}

.contract-link {
    text-decoration: underline;
    text-decoration-color: #64acff;
    word-break: break-all;
}

.contract-link-box {
    background-color: #FFFFFFDD;
    padding: 10px;
    width: 50vw;
    margin: 0 auto;
    border-width: 8px;
    border-style: solid;
    border-color: #B667D0;
}