body {
  background-color: #FFFFFF;
  color: #000;
  background-image: url(../assets/FleurDeLisBackground.png);
  background-attachment: fixed;
  background-size: cover;
  font-size: 20px;
}

.metamask-error {
  position: relative;
  top: 0;
  left: 0;
  background: #FBD34D;
  color: black;
  width: 100%;
  padding: 15px;
  text-align: center;
}

.App {
  text-align: center;
}

.container {
  height: 100%;
  background-color: #FFFFFFAA;
  padding: 0;
  width: 100%;
  margin: 0px auto;
}

.content, .home-content {
  max-width: 750px;
  margin: 0 auto;
}

.home-content {
  padding: 20px;
  background-color: #ffffffdd;
  border-width: 8px;
  border-style: solid;
  border-color: #B667D0;
}

.about-note {
  font-size: 20px;
  font-style: italic;
  color: #555555;
}

.guide {
  font-size: 16px;
  line-height: 24px;
}

.guide a {
  color: white;
}

.banner-img {
  background-image: url(../assets/HiTopsBanner.png);
  background-size: contain;
  background-repeat: repeat-x;
  margin-bottom: 50px;
  height: 255px;
  width: 100vw;
}

.banner-img img {
  max-height: 250px;
  max-width: 100%;
}

.cta-button {
  height: 45px;
  border: 0;
  width: auto;
  padding: 10px 55px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  margin-bottom: 25px;
}

.connect-wallet-button {
  background: -webkit-linear-gradient(-70deg,#a2facf,#64acff);
  background-size: 200% 200%;
  animation: gradient-animation 4s ease infinite;
}

.mint-button {
  background: -webkit-linear-gradient(-70deg,#a2facf,#64acff);
  background-size: 200% 200%;
  animation: gradient-animation 4s ease infinite;
  margin-right: 15px;
}

.reserve-nft-button, .withdraw-button, .close-presale-button {
  margin: 0 5px 5px 5px;
}

.discount-note {
  margin: 0 0 5px 0;
  color: #2dda53;
  font-weight: 700;
}

.discount-note em {
  color: #fc0000;
}

.kicks-header-block {
  width: 50vw;
  background-color: #FFFFFFAA;
  border-width: 8px;
  border-style: solid;
  border-color: #B667D0;
}

.kicks-image-block {
  width: 48vw;
  padding: 20px;
}

.single-kicks-image {
  border-width: 8px;
  border-style: solid;
  border-color: #B667D0;
}

.kicks-data-block {
  width: 50vw;
  background-color: #ffffffdd;
  padding: 20px;
  border-width: 8px;
  border-style: solid;
  border-color: #B667D0;
}

.opensea-button {
  background-color: rgb(32, 129, 226);
}

.mint-count {
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
  margin-top: 50px;
}

.twitter-logo {
  width: 35px;
  height: 35px;
}

.footer-text {
  color: white;
  font-size: 16px;
  font-weight: bold;
}

/* KeyFrames */
@-webkit-keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-moz-keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.mining {
  display:flex;
  justify-content: center;
  align-items: center;
}

.mining span, .mining div {
  display: inline-block;
}

.mining span {
  color: #FBD34D;
}

.success, .mining {
  margin-bottom: 20px;
}

.success button {
  display: inline-block;
  margin: 5px auto;
  text-align: center;
  background: -webkit-linear-gradient(-70deg, #a2facf 0%, #64acff 100%);   
  padding: 8px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.success p {
  color: #34D399;
}

.error p {
  color: red;
}

.loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid #FBD34D; /* Yellow */
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 2s linear infinite;
  margin: 0px 15px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.success-link {
  line-height: 27px;
}

.success-link span{
  color: white;
}

.success-link a {
  color: white;
}

/* Media Queries */
@media only screen and (max-width: 600px) {
  .heading {
    font-size: 25px;
    margin: 85px auto 0;
  }
  .metamask-error {
    width: unset;
  }
  .at-expanding-share-button[data-position=bottom-right] {
    bottom: 30px;
    right: 20px;
  }
  #at4-follow, #at4-foc {
    right: 10px;
  }
  .home-content, .footer-img img, .contract-link-box {
    border-color: crimson;
    border-image: none;
  }
}